html {
  font-family: "Open Sans", sans-serif; /* this should be the rest of the output you got from Google Fonts */
  background-color: #181818;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.text {
  margin: 0;
  font-size: 21px;
  align-items: stretch;
  color: white;
}

.nav {
  color: white;
  display: flex;
  align-items: stretch;
  gap: 30%;
  /*border: solid;
  border: #000000;
  border: 300px;*/
}

.site-title {
  /*font-size: 2rem;*/
  color: white;
  text-decoration: none;
  font-size: 3vw;
}

.nav ul {
  justify-content: space-evenly;
  padding-left: 30px;
  margin: 10px;
  list-style: none;
  display: flex;
  gap: 10%;
}

.nav a {
  color: inherit;
  font-size: 2vw;
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 0.2rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border-left: 3px solid;
  border-right: 3px solid;
}

.nav li.active {
  /*background-color: #222222;*/
  color: red;
}

.nav li:hover {
  /*background-color: #141414;*/
  color: rgb(141, 48, 48);
}

.welcome-title {
  color: white;
  /*display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;*/
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.enter-button {
  background-color: rgba(255, 0, 0, 0.411);
  text-decoration: none;
  color: inherit;
  padding: 0.3rem;
  position: fixed;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.splash {
  background-image: url("../bokeh.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.project-container {
  display: flex;
  justify-content: start;
  gap: 5%;
  color: whitesmoke;
  padding: 40px;
  border-color: #300e0e;
  border-style: solid;
  border-top: 10px solid;
  border-bottom: 10px solid;
  background-color: #222222;
}

.archives {
  color: white;
  display: block;
  align-items: center;
  border-bottom: 10px;
}

.newest-project {
  background-color: rgb(44, 0, 0);
  color: white;
  display: flex;
  align-items: center;
  gap: 10%;
  margin: 50px;
}

.centered-thing {
  text-align: center;
  color: white;
  margin-top: 10%;
}

.more-info-button {
  color: white;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.2rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

.more-info-button a {
  color: white;
  text-decoration: none;
}

.make-navbar-horizontal {
  display: flex;
  justify-content: start;
  gap: 5%;
  color: whitesmoke;
  padding: 40px;
  background-color: #000000;
}